.gallery-grid-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.gallery-grid {
	display: grid;
	grid-template-columns: repeat(9, 1fr);
	grid-column-gap: 17px;
	grid-row-gap: 17px;
	width: 100%;
	max-width: var(--wp--style--global--content-size);

	.gallery-item {
		height: 400px;

		&:nth-of-type(1) {
			grid-area: 1 / 1 / 2 / 6;
		}
		&:nth-of-type(2) {
			grid-area: 1 / 6 / 2 / 10;
		}
		&:nth-of-type(3) {
			grid-area: 2 / 1 / 3 / 4;
		}
		&:nth-of-type(4) {
			grid-area: 2 / 4 / 3 / 7;
		}
		&:nth-of-type(5) {
			grid-area: 2 / 7 / 3 / 10;
		}
		&:nth-of-type(6) {
			grid-area: 3 / 1 / 4 / 4;
		}
		&:nth-of-type(7) {
			grid-area: 3 / 4 / 4 / 10;
		}
		&:nth-of-type(n + 8) {
			grid-row: auto / span 1;
			grid-column: auto / span 3;
		}

		img {
			width: 100% !important;
			height: 100% !important;
			object-fit: cover;
			object-position: center;
		}
	}
}

.gallery-show-more {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 50px;
	width: 150px;
	border: none;
	border-radius: 0;
	font-weight: bold;
	font-size: var(--wp--preset--font-size--medium, 25px);
	font-family: var(--wp--preset--font-family--primary);
	background-color: var(--wp--preset--color--cyan-700, #265164) !important;
	color: white !important;
	margin-top: 110px !important;
	cursor: pointer;
	transition: all 0.3s;
	&:hover {
		background-color: var(--wp--preset--color--surface, #eeede6) !important;
		color: var(--wp--preset--color--cyan-400, #6e8b92) !important;
	}
}
